.page-loading-screen {
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  width: 70px;
  height: 70px;
  bottom: 10vh;
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}

.loader-wrapper {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

  img {
    width: 80px;
    height: 80px;
  }
}

.notification-box {
  position: fixed;
  bottom: 15px;
  right: 30px;
  padding: 8px 15px;
  animation: slide-in-out 2.5s linear;
  transform: translateY(150%);
  opacity: 1;
  font-size: 0.875rem;
  background-color: rgba(255, 255, 255, 0.85);
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 0.25rem;
  z-index: 5;

  &.success {
    border-color: green;
    color: green;
  }

  &.failure {
    border-color: #ff7c7c;
    color: #f53737;
  }

  &.warning {
    border-color: #ffbf48;
    color: #ff9900;
  }
}

.modal-wrapper {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 20;
  animation: fadein 0.25s ease-in-out;

  &.fade-out {
    animation: fadeout 0.25s ease-in-out forwards;
  }

  .modal-content {
    background: white;
    max-width: 90%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
    position: relative;
    animation: scalein 0.3s ease-in-out;

    & > * {
      max-width: 100%;
    }

    .dismiss {
      font-size: 32px;
      width: 44px;
      padding: 0;
      position: absolute;
      top: 5px;
      right: 5px;
      z-index: 15;
    }
  }

  &.fade-out .modal-content {
    animation: scaleout 0.3s ease-in-out forwards;
  }
}

.menus-toggler {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0);
  opacity: 0;
}

.confirmation-modal {
  flex-direction: column;
  padding: 30px;
  text-align: center;

  button.simple-button {
    border: 1px solid #b0b0b0;
    margin: 0 5px;
    background: white;
    /* border: 0; */
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    padding: 8px 22px;
    border-radius: 10px;

    &.confirm:hover {
      background: #19a000;
    }

    &.cancel:hover {
      background: #ff3838;
    }
  }

  h4 {
    margin-bottom: 30px;
    font-size: 1.7rem;
  }
}
